body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.header {
    margin-top: 20px;
    display: flex;
    flex: 1 1;
  }
  
  .header-top {
    display: flex;
    width: 100%;
    height: 80px;
  }
  
  .header-top__logo {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 20px;
  }
  
  .header-logo {
    color: inherit;
    text-decoration: inherit;
    font-family: RobotoThin;
    font-size: 30px;
  }
  
  .header-bottom {
    display: flex;
    flex: 1 1;
    justify-content: flex-end;
    align-items: center;
    margin-top: 6px;
  }
  
  .header-bottom__email {
    margin-right: 55px;
  }
  
  .header-bottom__phone {
    margin-right: 65px;
  }
  
  .header-bottom__bag {
    position: absolute;
    display: flex;
  }
  
  .header-bottom__bag-icon {
    position: relative;
    display: flex;
    left: -5px;
    width: 30px;
    height: 30px; 
  }
  
  .header-bottom__bag-count {
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: RobotoRegular;
    right: 0px;
    top: -5px;
    width: 20px;
    height: 20px;
    background-color: red;
    border-radius: 10px;
    color: white;
  }

.navbar {
    display: flex;
    flex-direction: row;
  }
  
.navbar-item {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 30px;
    color: inherit;
    text-decoration: inherit;
    text-transform: uppercase;
    outline: none;
}
:root{
  --pink-gradient-start: #fb61ba;
  --pink-gradient-end: #ff839d;
  --blue-gradient-start: #459bdb;
  --blue-gradient-end: #6f12e0;

  --sidebar-inactive: #f6e9ef;
  --sidebar-active: #fff5f7;
}

.sidebar {
    display: flex;
    flex-direction: column;

    position: fixed;
    left: 0;
    top: 25%;

    margin: 0;
    height: 50vh;
    width: 12rem;
    justify-content: center;

    background-color: #f6e9ef;

    background-color: var(--sidebar-inactive);
    border-top-right-radius: 2rem;
    border-bottom-right-radius: 2rem;
}
  
.sidebar-item {
    display: flex;
    flex-direction: column;
    padding: 0.5rem 0;
    margin: 0;
    text-align: left;
    color: inherit;
    text-decoration: inherit;
    outline: none;
    height: -webkit-min-content;
    height: min-content;

    box-shadow: 0px 1rem 0.5rem -1rem;
}

.sidebar-item.active{
    height: 100%;
    width: 100%;
    background-color: #fff5f7;
    background-color: var(--sidebar-active);
    border-bottom-right-radius: 1rem;
}

.sidebar-listheader{
    margin: 0;
    width: 100%;
    text-align: inherit;
    padding: 0.5rem 0;
    padding-left: 1rem;
}

.active .sidebar-listheader{
    width: 12rem;
    display: block;
    color: white;
    background-color: #459bdb;
    background-color: var(--blue-gradient-start);

    border-top-right-radius: 2rem;
    border-bottom-right-radius: 2rem;
}

.list-items-container{
    margin-top: 0.5rem;
    margin-left: 1rem;
    display: none;
    overflow: hidden;
}

.active .list-items-container{
    height: 100%;

    display: flex;
    flex-direction: column;
}

.list-item{
    margin: 0.25rem;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

